import React,{Component } from 'react';
import Code from 'react-markdown';
import demo1 from '../images/demo1.png';
import demo1_1 from '../images/demo1_1.png';
import demo2 from '../images/demo2.png';
import demo2_1 from "../images/demo2_1.png";
import {
    Layout,
    Menu,
    Icon,
    Timeline,
    Row,Col,
    Card
} from 'antd';
const {
    Header, Footer, Sider, Content,
  } = Layout;
const {Meta} = Card;
class  CssAnimated extends Component {
    render(){
        return (
            <Layout style={{marginLeft:200}}>
                <Header style={{ background: '#fff', padding: 0,paddingLeft:20 ,fontSize:20,fontWeight:900}} >
                    <h2> CSS动画与3D</h2> 
                </Header>
                <Content style={{ margin: '24px 16px 0', overflow: 'inherit'}}>
                <div style={{padding:24,backgroundColor:'#fff',height: ' calc(100vh - 160px)',overflowY:'auto'}}>
                    <Timeline>
                        <Timeline.Item>
                            <h3>3D转换</h3>
                            <ol>
                                <li>
                                    <p>定义向元素应用 2D 或 3D 转换，可以对元素进行移动、缩放、转动、拉长或拉伸</p>
                                    <ul type="circle">
                                        <li>
                                            <p>translate(移动) 根据X轴和Y轴位置给定的参数，从当前元素位置移动</p>
                                            <pre><Code source="transform: translate(100px,200px);"></Code></pre> 
                                        </li>
                                        <li>
                                            <p>rotate（旋转） 给定度数顺时针旋转的元素。负值则为逆时针旋转</p>
                                            <pre><Code source="transform: rotate(60deg);"></Code></pre>
                                        </li>
                                        <li>
                                            <p>scale (缩放) 对元素减小或者放大，取决于宽度（X轴）和高度（Y轴）的参数</p>
                                            <pre><Code source="transform: scale(2,2);"></Code></pre>
                                        </li>
                                        <li>
                                            <p>skew (倾斜) 根据X轴和Y轴位置给定的角度参数，进行倾斜，负数则为反方向倾斜</p>
                                            <pre><Code source="transform: skew(40deg,50deg);"></Code></pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <p>transform-origin：允许改变被转换元素的相对位置</p>
                                    <pre><Code source="transform-origin: x-axis y-axis z-axis;"></Code></pre>
                                </li>
                                <li>
                                    <p>transform-style：规定被嵌套元素如何在 3D 空间中显示</p>
                                    <pre><Code source="
                                        // preserve-3d所有子元素在3D空间中呈现
                                        // flat所有子元素在2D平面呈现
                                        transform-style: flat|preserve-3d;"></Code></pre>
                                </li>
                                <li>
                                    <p>perspective:设置元素距离视图的距离，以像素计，与 perspective-origin 属性一同使用，能够改变 3D 元素的底部位置</p>
                                    <pre><Code source="perspective: number|none;"></Code></pre>
                                </li>
                                <li>
                                    <p>backface-visibility:定义当元素不面向屏幕时是否可见，在旋转元素不希望看到其背面时，该属性很有用</p>
                                    <pre><Code source="backface-visibility: visible|hidden;"></Code></pre>
                                </li>
                            </ol>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>CSS3过渡（transition）</h3>
                            <p>1.某种效果可以从一种样式转变到另一种样式的效果</p>
                            <pre><Code source="transition: property duration timing-function delay;"></Code></pre>
                            <pre><Code source="transition-timing-function: linear|ease|ease-in|ease-out|ease-in-out|cubic-bezier(n,n,n,n);"></Code></pre>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>CSS3 动画（animation及@keyframes）</h3>
                            <p>1.使元素从一种样式逐渐变化为另一种样式的效果</p>
                            <ul type="circle"> 
                                <li>animation</li>
                                <li>@keyframes</li>
                                <li>语法规则及拆解
                                    <pre><Code source="
                                        animation: animation-name animation-duration animation-timing-function animation-fill-mode animation-delay animation-iteration-count	animation-direction animation-play-state;
                                        animation-name(keyframe名字): keyframename|non;
                                        animation-duration(动画完成一个周期需要多少秒或毫秒):animation-duration: time | 0;
                                        animation-timing-function(动画的速度曲线,使用的数学函数，称为三次贝塞尔曲线，速度曲线): linear	| ease	| ease-in | ease-out	| ease-in-out	 | cubic-bezier(n,n,n,n);
                                        animation-fill-mode(当动画完成时或又一个延迟未开始播放时的样式): none|forwards|backwards|both|initial|inherit;
                                        animation-delay(动画什么时候开始): time;
                                        animation-iteration-count(动画被播放的次数):n |infinite;
                                        animation-direction(是否循环交替反向播放动画): normal|reverse|alternate|alternate-reverse|initial|inherit;
                                        animation-play-state(动画是否正在运行或暂停):paused|running; "></Code></pre>
                                </li>
                            </ul>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>几则小demo</h3>
                            <ul type="square">
                                <li>
                                    <h4>demo1</h4>
                                    <Row>
                                        <Col span={12}>
                                            <ol type="I">
                                                <li>
                                                    <p>这种方法的思路是1.扭转外面的盒子 2.将图片转回并放大到正好盖住外面盒子大小 3.外面盒子隐藏超出部分</p>
                                                    <pre>
                                                        <Code source={
                                                            `html结构
    <div class="test">
        <img src="images/dog.png">
    </div> 

    1.扭转外面test 盒子  2.将图片再转回来
    <style>
    .test{
      margin: 0 auto;
      width: 400px;
      margin-top: 300px;
      height: 400px;
      background-color: beige;
      transform: rotate(45deg)
    }
    img{
      transform: rotate(-45deg)
    }
  </style>  

    图片放大倍数的计算：
    wdo = img.width = 400px; //原来的大小
    wdi = Math.sqrt(2)*400px; //将要放大的大小  即图中  X 的大小
    scale = wdi/wdo = Math.sqrt(2);

    所以  修改
    img{
        transform: rotate(-45deg) scale(1.41);
    }   
    即可实现 效果
                                                            `
                                                        }></Code>
                                                    </pre>
                                                </li>
                                                <li> 
                                                    <p>方法二 利用cli-path 属性对图片直接进行裁剪实现 简单粗暴</p>
                                                    <p>属性具体方法<a href="https://developer.mozilla.org/zh-CN/docs/Web/CSS/clip-path"> 参考链接 </a></p>
                                                    <pre>
                                                        <Code source='
                                                            img{
                                                                clip-path: polygon(50% 0,100% 50%,50% 100%,0 50%);
                                                                transition: 1s clip-path;
                                                              }
                                                              img:hover{
                                                                clip-path: polygon(0 0, 100% 0, 100% 100%,0 100%);
                                                              }
                                                        '></Code>
                                                    </pre>
                                                </li>
                                            </ol>
                                        </Col>
                                        <Col span={12}>
                                            <Col span={10}>
                                                <Card
                                                    bordered = {false}
                                                    style={{textAlign:'center'}}
                                                    cover={<img src={demo1} />}
                                                >
                                                    <Meta
                                                        title="效果图"
                                                    />
                                                </Card>
                                            </Col>
                                           <Col span={10}>
                                           <Card
                                                    bordered = {false}
                                                    style={{textAlign:'center'}}
                                                    cover={<img src={demo1_1} />}
                                                >
                                                    <Meta
                                                        title="图Ⅰ"
                                                    />
                                                </Card>
                                           </Col>
                                        </Col>
                                    </Row>
                                </li>
                                <li>
                                    <h4>demo2</h4>
                                    <p>用一个元素实现该效果</p>
                                    <Col span={12}></Col>
                                    <Col span={12}>
                                            <Col span={10}>
                                                <Card
                                                    bordered = {false}
                                                    style={{textAlign:'center'}}
                                                    cover={<img src={demo2} />}
                                                >
                                                    <Meta
                                                        title="效果图"
                                                    />
                                                </Card>
                                            </Col>
                                           <Col span={10}> 
                                           <Card
                                                    bordered = {false}
                                                    style={{textAlign:'center'}}
                                                    cover={<img src={demo2_1} />}
                                                >
                                                    <Meta
                                                        title="图Ⅰ"
                                                    />
                                                </Card>
                                           </Col>
                                        </Col>
                                        
                                </li>
                            </ul>
                        </Timeline.Item>
                    </Timeline>      
                </div>
                </Content>
                <Footer style={{ textAlign: 'center' }}>
                    This is my study note,welcome to comunicated with me , my wechat is "kannihappy"
                </Footer>
            </Layout>   
        )
    }
}
export default  CssAnimated;