import React,{Component } from 'react';
import Code from 'react-markdown';
import stack from '../images/javascript-stack.png';
import stack_object from '../images/javascript-stack1.png';
import {
    Layout,
    Menu,
    Icon,
    Timeline
} from 'antd';
const {
    Header, Footer, Sider, Content,
  } = Layout;
class  JsBasic extends Component {
    render(){
        return (
            <Layout style={{marginLeft:200}}>
                <Header style={{ background: '#fff', padding: 0,paddingLeft:20 ,fontSize:20,fontWeight:900}} >
                    <h2> 基础知识</h2>
                </Header>
                <Content style={{ margin: '24px 16px 0', overflow: 'inherit'}}>
                <div style={{padding:24,backgroundColor:'#fff',height: ' calc(100vh - 160px)',overflowY:'auto'}}>
                    <Timeline>
                        <Timeline.Item>
                            <h3>基本数据类型与引用数据类型</h3>
                            <ol>
                                <li>
                                    <p>基本数据类型</p>
                                    <ul>
                                        <li>概念：基本数据类型是按值进行访问，变量是放在栈（stack）内存中</li>
                                        <li>种类：undefined、null 、Boolean 、String 、Number、Symbol</li>
                                        <li>基本数据类型的值是不可变的
                                            <pre><Code source="
                                                ```js
                                                    var str = 'richard';
                                                    str.toUpperCase();//Richard
                                                    console.log(str);//richard
                                                ```
                                            "></Code></pre>
                                        </li>
                                        <li>
                                            <p>按值进行比较</p>
                                            <pre><Code source="
                                                ```js
                                                    var a = 1;
                                                    var b = true;
                                                    console.log(a == b); //true
                                                    console.log(a === b);false
                                                ```
                                            "></Code></pre>
                                            <ul type="circle">
                                                <li>虽然数据类型不同（true属于Boolean,1为Number）但在比较之前js自动进行了数据类型的隐式转换</li>
                                                <li> == 是进行值比较所以为true</li>
                                                <li>=== 不仅比较值还要比较数据类型所以为false</li>
                                            </ul>
                                        </li>
                                        <li>
                                            <p>栈内存中保存了变量的标识符和变量的值</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                        var a,b;
                                                        a = 1;
                                                        b = a;
                                                        console.log(a); //1
                                                        console.log(b);//1
                                                        a = 2;
                                                        console.log(a);//2
                                                        console.log(b);//1
                                                    ```
                                                "></Code>
                                            </pre>
                                            <img src={stack} />
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <p>引用数据类型</p>
                                    <ul>
                                        <li>概念:引用类型的值保存在堆内存（Heap）中的对象（Object）</li>
                                        <li>种类：统称为Object，细分有：Object,Array,Function,Data,RegExp等</li>
                                        <li>
                                            <p>引用类型的值是可变化的</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                        var obj = {name:'richard'};
                                                        obj.name = 'wangrenjie';
                                                        obj.age = 26;
                                                        obj.say = function(){
                                                            return 'My name is' + this.name + ‘I'm ’ + this.age + 'years old';
                                                        }
                                                        obj.say(); //My name is wangrenjie I‘m 26 years old
                                                    ```
                                                "></Code>
                                            </pre>
                                        </li>
                                        <li>
                                            <p>按引用地址比较</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                        var obj = {};
                                                        var obj1 = {};
                                                        console.log(obj == obj1); //false
                                                        console.log(obj === obj1) //false
                                                    ```
                                                "></Code>
                                            </pre>
                                        </li>
                                        <li>栈内存中保存了变量标识符和指向堆内存中该对象的指针</li>
                                        <li>
                                            <p>堆内存中保存了对象的内容</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                    var a = {name:'richard'};
                                                    var b = a;
                                                    a.name = 'wangrenjie';
                                                    console.log(b.name); //wangrenjie
                                                    b.age = 26;
                                                    console.log(a.age) //26
                                                    b.say = function(){
                                                        return 'My name is ' + this.name + 'I'm' + this.age + 'years old';
                                                    }
                                                    console.log(a.say());//My name is wangrenjie I'm 26 years old;
                                                    var c = {
                                                        name:'wagnrenjie',
                                                        age:26
                                                    }
                                                    ```
                                                "></Code>
                                            </pre>
                                            <img src={stack_object}/>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <p>类型检测</p>
                                    <ul>
                                        <li>
                                            <p>typeof: 经常检查变量是不是基本数据类型</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                        var a;
                                                        a = 'hello';
                                                        typeof a; //String
###
                                                        a = true;
                                                        typeof a;//Boolean
###
                                                        a = 1;
                                                        typeof a;//Number
###
                                                        typeof a ;// undefined
###
                                                        a = symbol();
                                                        typeof a ;// Symbol
###
                                                        a = function(){};
                                                        typeof a; //function
###
                                                        a = [];
                                                        typeof a; //object
###
                                                        a = {};
                                                        typeof a;//object
###
                                                        a = /richard/g;
                                                        typeof a ;//object
                                                    ```
                                                "></Code>
                                            </pre>
                                        </li>
                                        <li>
                                            <p>instanceof：经常用来判断引用类型的变量具体是某种类型</p>
                                            <pre><Code source="
                                                ```js
                                                    var a ;
                                                    a = function(){}
                                                    a instanceof Function; //true
                                                    a = [];
                                                    a instanceof Array ;//true
                                                    a = {};
                                                    a instanceof Object;//true
                                                    a = /richard/g;
                                                    a instanceof RegExp;//true
                                                ```
                                            "></Code></pre>
                                        </li>
                                    </ul>
                                </li>
                            </ol>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>变量提升与函数提升</h3>
                            <ol>
                                <li>
                                    <p>js代码虽然是逐行向下执行的，但执行的时候分为两个步骤</p>
                                    <ul type="circle">
                                        <li>编辑阶段（词法解释/预解释）</li>
                                        <li>执行</li>
                                    </ul>
                                </li>
                                <li>函数声明和变量声明总是会被解释器（编译阶段）悄悄地被提升到最顶部，然后才执行</li>
                                <li>在js中变量编译的顺序   this和arguments(语言内置) -- 函数的形式参数 -- 函数声明 -- 变量声明</li>
                                <li>函数的声明比变量的优先级高</li>
                                <li>es6中let关键字及块级作用域</li>
                                <li><p>变量提升</p>
                                    <ul>
                                        <li>
                                            <p>demo1</p>
                                            <pre><Code source={
                                                `
            a = 'richard';
            var a;
            console.log(a);//richard

            //编译后的代码
            var a ;
            a = 'richard';
            console.log(a);
                                                `
                                            }></Code></pre>
                                        </li>
                                        <li>
                                        <pre>
                                            <Code source={
                                                `
        function demo(){
            a = 'richard';
            console.log(a);
            console.log(window.a);
            var a = "wangrenjie";
            console.log(a)
        }
        demo();

        //编译后的代码
        function demo(){
            var a;
            a = 'richard';
            console.log(a);
            console.log(window.a);
            a = 'wangrenjie';
            console.log(a);
        }
        demo();// richard undefined wangrenjie
                                                `
                                            }></Code>
                                        </pre>
                                    </li>
                                    </ul>
                                </li>
                                <li>
                                    <p>函数提升</p>
                                    <ul>
                                        <li>
                                            <p>函数定义的两种方式</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                    var fn = function fn(){} // 函数表达式  显示声明
                                                    function  fn(){} // 函数式声明  隐式声明
                                                    ```
                                                "></Code>
                                            </pre>
                                        </li>
                                        <li>
                                            <p>只有声明方式是函数式声明才会有变量提升</p>
                                            <pre>
                                                <Code source="
                                                    ```js
                                                        test();
                                                        function test(){
                                                            console.log(a);
                                                            var a = 'richard';
                                                        }

                                                        //编译之后
                                                        function test(){
                                                            var a ; //在函数作用域内，被提升最前面
                                                            console.log(a);//undefined
                                                            a = 'richard';
                                                        }
                                                        test();
                                                    ```
                                                "></Code>
                                            </pre>
                                        </li>
                                    </ul>
                                </li>
                            </ol>
                            
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>闭包</h3>
                            <p>说起闭包那么首先先来了解一下js执行环境（execution context）、作用域以及作用域链（scope chain）</p>
                           <ol>
                               <li>
                                   <p>执行环境定义了变量或者函数有权访问的其他数据，决定了各自的行为</p>
                                   <ul type="circle">
                                        <li>
                                            <p>组成部分</p>
                                            <ul type="square">
                                                <li>变量对象（varible object）：存放当前执行环境中定义的变量和函数，如果当前环境是函数则将活动对象（activation object）作为变量对象，但不包括函数表达式</li>
                                                <li>作用域链：当代码在执行环境中运行时，会创建变量对象的一个作用域链（词法作用域），能够保证对执行环境中所有变量和函数有序访问</li>
                                                <li>this指向：根据调用的规则不同this指向不同产生的执行环境上下文不同</li>
                                            </ul>
                                        </li>
                                        <li>
                                            <p>类型</p>
                                            <ul type="square">
                                                <li>全局执行环境</li>
                                                <li>局部执行环境</li>
                                            </ul>
                                        </li>
                                        <li>
                                            <p>执行过程</p>
                                            <ul type="square">
                                                <li>在全局代码执行前，js引擎就会创建一个栈来存储管理所有的执行上下文对象</li>
                                                <li>在全局执行环境（window）确认后，将其push到栈中</li>
                                                <li>在函数执行环境创建后，将其push到栈中</li>
                                                <li>在当前函数执行完后，将栈顶的对象pop</li>
                                                <li>当所有的代码执行完后，栈中执行下window
                                                    <pre>
                                                        <Code source="
                                                            ```js
                                                            /*
                                                            *demo1:  
                                                            */
                                                           function a() {}
                                                            var a
                                                            console.log(typeof a) // 'function'

                                                            /*
                                                            *demo2:
                                                            */

                                                           if (!(b in window)) {
                                                            var b = 1
                                                            }
                                                            console.log(b) // undefined
                                                            编译后
                                                            var b;
                                                            if(!(b in window)){
                                                                b = 1;
                                                            }
                                                            console.log(b);

                                                            /*
                                                            *demo3:
                                                            */
                                                           console.log(a);    // f a() {console.log(10)}
                                                           console.log(a());    //  undefined  没有返回值
                                                           var a = 1;
                                                           function a() {
                                                             console.log('nihao') //nihao
                                                           }
                                                           console.log(a)   //1
                                                           a = 3;
                                                           console.log(a());  //a is not a function;

                                                           /*
                                                           *demo4:
                                                           */
                                                           function fun(){ var a=b=3;}
                                                           console.log(b)//B是全局变量 var a是局部变量  b=3;a=undefine
                                                            ```
                                                        "></Code>
                                                    </pre>
                                                </li>
                                            </ul>
                                        </li>
                                   </ul>
                               </li>
                               <li>
                                   <p>作用域</p>
                                   <ul type="circle">
                                        <li>概念：执行上下文中声明的变量和声明的作用范围。</li>
                                        <li>
                                            <p>分类：</p>
                                            <ul type="square">
                                                <li>全局作用域</li>
                                                <li>函数作用域</li>
                                                <li>es6块级作用域</li>
                                            </ul>
                                        </li>
                                        <li>作用：隔离变量，不同作用域下同名变量不会有冲突</li>
                                        <li>
                                            <p>区别：</p>
                                            <ul type="square">
                                                <li>创建时机不同，全局执行上下文是在全局作用域确定后js代码执行前创建而函数执行上下文是在调用函数时，函数体代码执行之前创建</li>
                                                <li>作用域是静态的，执行上下文是动态的</li>
                                            </ul>
                                        </li>
                                   </ul>
                               </li>
                               <li>
                                   <p>作用域链</p>
                                   <ul>
                                       <li>从内到外多个作用域形成的链</li>
                                       <li>
                                           <p>包含父级([[scope]])变量对象与作用域链和自身的变量对象(如果是函数则为活动对象AO)</p>
                                           <pre>
                                               <Code source="
                                                    ```js
                                                        var x = 10;
                                                        function fn () {
                                                        console.log(x);
                                                        }
                                                        function show (fn) {
                                                        var x = 20;
                                                        fn();
                                                        }
                                                        show(fn) // 10;         
                                                    ```
                                               "></Code>
                                           </pre>
                                       </li>
                                   </ul>
                               </li>
                               <li>
                                   <p>闭包</p>
                                   <ul type="circle">
                                        <li>概念：能够访问其他函数内变量的函数，是一个比较特殊的作用域函数</li>
                                        <li>
                                            <p>作用</p>
                                            <ul type="square">
                                                <li>
                                                    <p>匿名自执行函数，减少内存消耗</p>
                                                    <pre>
                                                        <Code source="
                                                        ```js
                                                            (function ($) {})(jQuery);   
                                                        ```
                                                        "></Code>
                                                    </pre>
                                                </li>
                                                <li>
                                                    <p>缓存计算结果</p>
                                                    <pre>
                                                        <Code source="
                                                            ```js
                                                                var fun1 = function(){
                                                                    var a=1;
                                                                    return function fun2(){
                                                                    a++;
                                                                    alert(a)
                                                                    }
                                                                }
                                                                var b = fun1();
                                                                b(); // 2       
                                                                b(); // 3           
                                                            ```
                                                        "></Code>
                                                    </pre>
                                                </li>
                                                <li>
                                                    <p>封装，管理私有方法和变量，避免全局变量冲突污染</p>
                                                    <pre>
                                                        <Code source='
                                                            ```js
                                                            var person = function(){    
                                                                var name = "renbo";       
                                                                return {    
                                                                  getName : function(){    
                                                                    return name;    
                                                                  },    
                                                                  setName : function(newName){    
                                                                    name = newName;    
                                                                  }    
                                                                }    
                                                              }()    
                                                            ```
                                                       ' ></Code>
                                                    </pre>
                                                </li>
                                                <li>实现类和继承等等</li>
                                            </ul>
                                        </li>
                                        <li>
                                            <p>缺点：</p>
                                            <ul type="square">
                                                <li>由于变量对象一直在内存中引用不被释放，导致内存过高。</li>
                                                <li>由于多个函数共享一个父级，当父级有变量更改时，所有子函数受影响</li>
                                            </ul>
                                        </li>
                                   </ul>
                               </li>
                           </ol>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>关键字this</h3>
                            <ol>
                                <li>概念：js的关键字，在执行上下文环境时被绑定。</li>
                                <li>绑定规则： 默认绑定、隐式绑定、apply,call、new绑定。优先级从低到高</li>
                                <li>
                                    <p>默认绑定</p>
                                    <pre>
                                        <Code source="
                                            ```js
                                                console.log(this); //window
                                                function fun() {
                                                console.log(this.a) // 非严格模式下1 ，严格模式下undefined
                                                }
                                                var a = 1;
                                                fun(); 
                                            ```
                                        "></Code>
                                    </pre>
                                </li>
                                <li>
                                    <p>隐式调用，作为对象方法调用，this 指代上下文对象</p>
                                    <pre>
                                        <Code source="
                                            ```js
                                            function fun() { 
                                                console.log( this.a );
                                              }
                                              var a = 1;
                                              var obj = { 
                                                a: 2,
                                                fun: fun 
                                              };
                                              obj.fun(); // 2
                                              var c = obj.fun;
                                              console.log(c()); // 1   
                                            ```
                                        "></Code>
                                    </pre>
                                </li>
                                <li>
                                    <p>apply,call,改变对象的prototype关联对象来改变this,对于null，undefined绑定会失效</p>
                                    <pre>
                                        <Code source="
                                            ```js
                                            function fun() { 
                                                console.log( this.a );
                                              }
                                              var a = 1;
                                              var obj1 = { 
                                                a: 2,
                                              };
                                              var obj2 = { 
                                                a: 3,
                                              };
                                              fun.call( obj1 ); // 2
                                              fun.call( obj2 ); // 3
                                              fun.call( null ); // 1
                                              fun.call( undefined ); // 1
                                            ```
                                        "></Code>
                                    </pre>
                                </li>
                                <li>
                                    <p>作为构造函数调用，this 指代new 出的对象 - 使用new来调用函数，会自动执行如下操作：</p>
                                    <ul type="circle">
                                        <li>创建一个全新的对象。</li>
                                        <li>这个新对象会被执行[[原型]]连接。</li>
                                        <li>这个新对象会绑定到函数调用的this。</li>
                                        <li>如果函数没有返回其他对象,那么new表达式中的函数调用会自动返回这个新对象。</li>
                                    </ul>
                                    <pre>
                                        <Code source="
                                            ```js
                                            function fun(a) { 
                                                this.a = a;
                                              }
                                              var a = 1;
                                              var obj = new fun(3);
                                              console.log(obj.a); // 3
                                              var obj1 = new fun(4);
                                              console.log(obj1.a); // 4
                                            ```
                                        "></Code>
                                    </pre>
                                </li>
                                <li>es6中箭头函数this指向 - 取决于外层（函数或全局）作用域。</li>
                            </ol>
                        </Timeline.Item>
                    </Timeline>      
                </div>
                </Content>
                <Footer style={{ textAlign: 'center' }}>
                    This is my study note,welcome to comunicated with me , my wechat is "kannihappy"
                </Footer>
            </Layout>   
        )
    }
}
export default  JsBasic;